@use '../../scss/abstracts';

.wp-block-md-pointcentral-fse-sample-block {
    background-color: abstracts.$green-theme-color;
    color: abstracts.$white-theme-color;
    padding: 5px 10px;
}

.pointcentral-video-banner {
    position: relative;
    // padding-top: 100px;
    // padding-bottom: 200px;
    color: rgb(255, 255, 255);
    z-index: 100;
    background-size: cover;
    width: 100%;
    height: auto;
    overflow: hidden;
    .pointcentral-video-banner__video {
        opacity: 1;
        height: auto;
        width: 100vw;
        position: absolute;
        top: 0;
        padding-bottom: 0;
        overflow: hidden;
        z-index: 1;
        .pointcentral-video-banner__video__inner {
            height: 100%;
            video {
                visibility: visible;
                height: 100%;
                width: 100%;
                opacity: 1;
            }
        }
    }
    .pointcentral-video-banner__overlay {
        opacity: 0.7;
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 2;
        top: 0;
    }
    .pointcentral-video-banner__content {
        padding-top: 100px;
        padding-bottom: 200px;
        z-index: 3;
        position: relative;
        .pointcentral-video-banner__sub-title {
            color: #ffffff;
            line-height: 21px;
            text-align: center;
            font-weight: 600;
            font-size: 38px;
        }
        .pointcentral-video-banner__title {
            color: #ffffff;
            line-height: 23px;
            text-align: center;
            font-weight: 800 !important;
            font-style: italic;
            margin: 42px 0 35px;
            font-size: 69px !important;
            text-transform: none !important;
        }
        .pointcentral-video-banner__description {
            color: #ffffff;
            line-height: 22px;
            text-align: center;
            margin-bottom: 8px;
            font-size: 18px;
            font-weight: 800;
        }
        .pointcentral-video-banner__button { 
            text-align: center;
            margin-top: 36px;
            color: rgb(30, 98, 170);
            a {
                visibility: visible;
                padding: 12px 18px;
                background-color: rgb(255, 255, 255);
                display: inline-block;
                color: inherit;
                text-decoration: none;
                display: inline-block;
            }
        }
    }
}